const providerUser = {
  namespaced: true,
  state: () => ({
    username: "lss0555",
    userInfo: {
      date_created: "def",
      mobile: "def",
      mail: "def",
    },
    userid: "00",
    auth_date: {
      last: "尚未认证",
      first: "尚未认证",
    },
  }),

  mutations: {
    setUserName(state, username) {
      state.username = username;
    },
    setUserInfo(state, userInfo) {
      state.userInfo = userInfo;
      console.log("store: ", state.userInfo);
    },
    setAuthDate(state, authDate) {
      state.auth_date = authDate;
      console.log("store: ", state.auth_date);
    },
    setUserID(state, userid) {
      state.userid = userid;
    },
  },

  getters: {
    getUserName(state) {
      return "my name is " + state.username;
    },
    getUserInfo(state) {
      return state.userInfo;
    },
    getUserId(state) {
      return state.userid;
    },
  },

  actions: {
    getUserInfofrom({ state, commit }) {
      commit("setUserInfo", {
        name: "def",
        mobile: "def",
        mail: "def",
      });
    },
  },
};

export default providerUser;
