let ms = require.context("./", false, /\w.js$/);
let modules = {};
ms.keys().forEach((item) => {
  let fileName = item.substring(2, item.length - 3);
  if (fileName == "index") {
    return;
  }
  modules[fileName] = ms(item).default;
});
console.log(modules)
export default {
   install: (app) => {
       for (let item in modules) {
      app.directive(item, modules[item]);
    }
  },
}
