<template>
  <transition name="myTransition">
    <div
      class="toast"
      :style="{
        'background-color': backgroundColor,
        width: selfWidth,
        left: selfLeft,
      }"
      v-show="isShow"
    >
      <p>{{ message }}</p>
    </div>
  </transition>
</template>

<script>
export default {
  name: "e-Toast",
  data() {
    return {
      isShow: false,
      backgroundColor: "#EEEEEE",
      message: "",
      duration: 2000,

      selfWidth: "170px",
      selfLeft: "0px",
    };
  },
  methods: {
    success(message, left = 0, width = 170, duration) {
      this.message = message;
      this.selfLeft = `${left}px`;
      this.selfWidth = `${width}px`;
      this.duration = duration || 2000;

      this.backgroundColor = "#93D500";
      this.isShow = true;
      setTimeout(() => {
        this.isShow = false;
      }, this.duration);
    },
    error(message, left = 0, width = 170, duration) {
      this.message = message;
      this.selfLeft = `${left}px`;
      this.selfWidth = `${width}px`;
      this.duration = duration || 2000;

      this.backgroundColor = "#FF0000";
      this.isShow = true;
      setTimeout(() => {
        this.isShow = false;
      }, this.duration);
    },
    warn(message, left = 0, width = 170, duration) {
      this.message = message;
      this.selfLeft = `${left}px`;
      this.selfWidth = `${width}px`;
      this.duration = duration || 2000;

      this.backgroundColor = "#FFCD00";
      this.isShow = true;
      setTimeout(() => {
        this.isShow = false;
      }, this.duration);
    },
  },
};
</script>

<style scoped>
.toast {
  position: fixed;
  top: 30px;
  left: 0;
  right: 0;
  margin: auto;
  width: 170px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  font-size: 16px;
  color: var(--color-impure-white);
  border-radius: 30px;
  z-index: 99;
}

.myTransition-enter-active,
.myTransition-leave-active {
  transition: all 0.4s;
}
/* 开始之前与结束之后的状态 */
.myTransition-enter,
.myTransition-leave-to {
  opacity: 0;
  transform: translateY(-200px);
}
</style>
