const productPage = {
  namespaced: true,
  state() {
    return {

      payload: {
        //所有的字段
      },
      info: {
        card1ID: {
          //card1的基本信息，包括数据合法性等
        },
        card2ID: {},
      },
      id:0,
      initstr:'',
      moduleStatus: [
        // {
        //   name: "",
        //   done: false,
        //   selected: true,
        // },
        // {
        //   name: {
        //},
        //   done: false,
        //   selected: true,
        // },
        // {
        //   name: "",
        //   done: false,
        //   selected: true,
        // },
      ],
      status:0,
      id:0,
      initstr:''
    };
  },
  mutations: {
    setPayload(state, partialPayload) {
      Object.keys(partialPayload).forEach((key) => {
        state.payload[key] = partialPayload[key];
      });
    },
    setInfo(state, cardID, partialInfo) {
      state.info[cardID] = partialInfo;
    },
    updateModuleStatus(state, newStatus) {
      state.moduleStatus = newStatus;
    },
    loadCache(state, val) {
      Object.keys(val).forEach(function (e) {
        state[e] = val[e];
      });
    },
    setStatus(state,st){
      state.status = st;
    },
        Initial(state,details){
      for (let i in details){
        state.payload[i]= {value:details[i]}
      }
      function hashVal(string){
        var hash = 0, i, chr;
        if (string.length === 0) return hash;
        for (i = 0; i < string.length; i++) {
          chr = string.charCodeAt(i);
          hash = ((hash << 5) - hash) + chr;
          hash |= 0; // Convert to 32bit integer
        }
        return hash;
      }
      state.initstr = hashVal(JSON.stringify(state.payload))
    },
    setId(state,id){
      state.id = id;
    },
    updateModule(state, newStatus) {
      state.moduleStatus[newStatus.moduleTitle] = newStatus;
    },
    deletePayload(state, emp) {
      state.payload = {};
    },
    setStatus(state,st){
      state.status = st;
    },
    Initial(state,details){
      for (let i in details){
        state.payload[i]= {value:details[i]}
      }
      function hashVal(string){
        var hash = 0, i, chr;
        if (string.length === 0) return hash;
        for (i = 0; i < string.length; i++) {
          chr = string.charCodeAt(i);
          hash = ((hash << 5) - hash) + chr;
          hash |= 0; // Convert to 32bit integer
        }
        return hash;
      }
      state.init = JSON.parse(JSON.stringify(state.payload))
    },
    setId(state,id){
      state.id = id;
}
  },

  getters: {},

  actions: {},
};

export default productPage;
