import { createApp } from "vue";
import Internal from "./internal.vue";
import router from "./routerPro";
import toast from "./components/common/toast";
import VueLazyLoad from "vue3-lazyload";
import { uni_print } from "./common/print";
import "ant-design-vue/dist/antd.css";

import {
  Pagination,
  message,
  Tooltip,
  Popconfirm,
  Empty,
  Result,
  Space,
  Spin,
  Modal,
  Input,
} from "ant-design-vue";
import "./static/antdReset.less";
import eventBus from "vue3-eventbus";
import cookieOperations from "./common/cookie";
// import Vuex from "vuex";
import store from "./store/store";
import clipboard from "clipboard";
import regFunc from "../index/asset-common/regFunc";
import picPreview from "./components/common/picPreview";
import directive from './views/providerTools/activityCreateForm/inputs/directive/index'
import ElementPlus from 'element-plus'
import 'element-plus/dist/index.css'
import * as ElementPlusIconsVue from '@element-plus/icons-vue'


regFunc();

