eventBus.$on = eventBus.on;
eventBus.$emit = eventBus.emit;
eventBus.$off = eventBus.off;
const app = createApp(Internal);
for (const [key, component] of Object.entries(ElementPlusIconsVue)) {
  app.component(key, component)
}
app
  // .use(message)
  .use(store)
  .use(router)
  .use(Pagination)

  .use(Tooltip)
  .use(Popconfirm)
  .use(Empty)
  .use(Result)
  .use(Space)
  .use(Spin)
  .use(Modal)
  .use(Input)
  .use(eventBus)
  .use(toast)
  .use(picPreview)
  .use(directive)
  .use(ElementPlus)
  .use(VueLazyLoad, {
    loading: require("../../assets/placeholder.png"),
  })

  .mixin({
    inject: [
      "$bus",
      "clipboard",
      "$print",
      "$message",
      "cookieOperations",
      "$set",
    ],
  })
  .provide("$bus", eventBus)
  .provide("clipboard", clipboard)
  .provide("$message", message)
  .provide("$set", function (obj, key, val) {
    obj[key] = val;
  })
  .provide("cookieOperations", cookieOperations)
  .provide("$print", uni_print)
  .mount("#internal");
