// eslint-disable-next-line import/extensions
import PicPreview from './PicPreview.vue';
import {createApp} from 'vue'

// v3.x
const createMount=options=>{
  const mountNode=document.createElement('div');
  document.body.appendChild(mountNode)
  const app=createApp(PicPreview,{
    ...options,
    remove(){
      app.unmount(mountNode)
      document.body.removeChild(mountNode)
    }
  })
  return app.mount(mountNode)
}
const picPreview=options=>{
  return createMount(options)
}
picPreview.install=app=>{
  app.component('e-PicPreview',PicPreview);
  app.config.globalProperties.$picPreview=picPreview()
}
export default picPreview



