<!-- 图片预览器，已注册成插件形式 -->
<template>
  <div>
    <div class="pic-preview" v-show="ifShowPicPreview">
      <div class="cancel-img" @click="hidePicPreview"></div>
    </div>

    <div class="show" v-show="ifShowPicPreview">
      <div class="show-main">
        <div class="left" v-show="list.length !== 1 && list.length !== 0">
          <img
            src="../../../assets/image/common/left.svg"
            @click="showLast()"
            v-show="activeIndex !== 0"
          />
          <img
            src="../../../assets/image/common/left-forbidden.svg"
            v-show="activeIndex === 0"
            style="cursor: not-allowed"
          />
        </div>
        <div class="center">
          <div class="img-zone">
            <img
              :src="getImageUrl(list[activeIndex])"
              @click="hidePicPreview"
              style="cursor: pointer"
            />
          </div>
        </div>
        <div class="right" v-show="list.length !== 1 && list.length !== 0">
          <img
            src="../../../assets/image/common/right.svg"
            @click="showNext()"
            v-show="activeIndex + 1 !== list.length"
          />
          <img
            src="../../../assets/image/common/right-forbidden.svg"
            v-show="activeIndex + 1 === list.length"
            style="cursor: not-allowed"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "e-PicPreview",
  data() {
    return {
      list: [],
      activeIndex: 0,
      ifShowPicPreview: false,
    };
  },
  methods: {
    showLast() {
      if (
        this.activeIndex - 1 >= 0 &&
        this.activeIndex - 1 <= this.list.length - 1
      ) {
        this.activeIndex -= 1;
      }
    },
    showNext() {
      if (
        this.activeIndex + 1 >= 0 &&
        this.activeIndex + 1 <= this.list.length - 1
      ) {
        this.activeIndex += 1;
      }
    },
    hidePicPreview() {
      this.ifShowPicPreview = false;
      this.list = [];
      this.activeIndex = 0;
    },
    getImageUrl(whatever) {
      if (Object.prototype.toString.apply(whatever) !== "[object Object]") {
        return whatever;
      } else {
        return whatever.url;
      }
    },
  },
};
</script>

