import logo from './w1.png'
class JSPrint {
    constructor() {
        this.targetType = "jpeg"
        this.opacity = 0.5
        this.dx = 10
        this.dy = 10
        this.fileName = "go.jpg"
        const canvas = document.createElement('canvas')
        this.canvas = canvas
        this.ctx = canvas.getContext("2d")

    }
    read(file) {
        if(!file) return
        const reader = new FileReader()
        return new Promise((resolve, reject) => {
            reader.readAsDataURL(file)
            reader.onload = function (evt) {
                resolve(evt.target.result)
            }
        })
    }
    getImage(base64) {
        return new Promise((resolve, reject) => {
            const ret = { img: new Image() }
            ret.img.src = base64
            ret.img.onload = function () {
                ret.width = this.width
                ret.height = this.height
                resolve(ret)
            }
        })
    }
    isBase64(file) {
        if (typeof file == "string") {
            return true
        } else {
            return false
        }
    }
    async setLogoSource(file) {
        if (this.isBase64(file)) {
            this.logoSource = await this.getImage(file)
        } else {
            const src = await this.read(file)
            this.logoSource = await this.getImage(src)
        }
    }
    setTarget(targetType) {
        this.targetType = targetType;
        return this
    }
    setLogoPos(dx, dy) {
        this.dx = dx
        this.dy = dy
        return this
    }
    setLogoOpacity(opacity) {
        this.opacity = opacity
        return this
    }
    setFileName(str) {
        this.fileName = str
        return this
    }
    async setOpacity() {
        if (this.opacity != 1) {
            const canvas = document.createElement('canvas')
            const ctx = canvas.getContext("2d")
            canvas.width = this.logoSource.width
            canvas.height = this.logoSource.height
            const img = new Image()
            ctx.globalAlpha = this.opacity
            ctx.fillStyle = 'rgba(255, 255, 255, 0)';
            ctx.fillRect(0, 0, this.logoSource.width, this.logoSource.height)
            ctx.drawImage(this.logoSource.img, 0, 0)
            img.src = canvas.toDataURL('image/png', 1)
            await new Promise((resolve, reject) => {
                img.onload = function () {
                    resolve()
                }
            })
            this.logoSource.img = img
        }
    }
    getImageElement(src) {
        const image = new Image()
        image.src = src
        return image
    }
    blobToFile(theBlob) {
        theBlob.lastModifiedDate = new Date();
        theBlob.name = this.fileName;
        return new File([theBlob], this.fileName,{type: `image/${this.targetType}`});
    }
    getBlobUrl(blob) {
        return window.URL.createObjectURL(blob)
    }
    getBlob() {
        return new Promise((resolve, reject) => {
            this.canvas.toBlob((res) => {
                resolve(res)
            }, `image/${this.targetType}`, 1)
        })
    }
    clear() {
        const w = this.canvas.width;
        const h = this.canvas.height;
        this.ctx.clearRect(0, 0, w, h);
    }
    async print(origin, targetType) {
        origin.name&&(this.fileName=origin.name)
        if (!this.logoSource) {
            const ret = await fetch("https://www.camptogo.com/media/watermark_small.png").then(r => r.blob()).catch(r=>{
            !r    
            this.setLogoSource(logo)
            // 线上没有跨域问题
            })
            if(!this.logoSource){
                this.setLogoSource(ret)
            }
        }
        if (this.canvas.width) {
            this.clear()
        }
        let str
        if (this.isBase64(origin)) {
            str = origin
        } else {
            str = await this.read(origin)
        }
        const imgOrigin = await this.getImage(str)
        this.canvas.width = imgOrigin.width
        this.canvas.height = imgOrigin.height
        this.ctx.drawImage(imgOrigin.img, 0, 0)
        const x = imgOrigin.width - this.logoSource.width - this.dx
        const y = imgOrigin.height - this.logoSource.height - this.dy
        await this.setOpacity()
        this.ctx.drawImage(this.logoSource.img, x, y)
        if (targetType == "base64") {
            return this.canvas.toDataURL(`image/${this.targetType}`, 1)
        } else if (targetType == "blob") {
            const ret = await this.getBlob()
            return ret
        } else if (targetType == "url") {
            const ret = await this.getBlob()
            return this.getBlobUrl(ret)
        } else if (targetType == "image") {
            return this.getImageElement(this.canvas.toDataURL(`image/${this.targetType}`, 1))
        } else if (targetType == "file") {
            const ret = await this.getBlob()
            return this.blobToFile(ret)
        }
    }
    async getPrintedUrl(origin) {
        return await this.print(origin, "url")
    }
    async getPrintedBase64(origin) {
        return await this.print(origin, "base64")
    }
    async getPrintedFile(origin) {
        return await this.print(origin, "file")
    }
    async getPrintedImage(origin) {
        return await this.print(origin, "image")
    }
}
export const uni_print = new JSPrint()